import Vue from 'vue'
import Router from 'vue-router'

import HelloWorld from '../components/HelloWorld.vue'
import Index from '../components/index'
import Personage from '../components/personage'
import Imya from '../components/imya'
import Invitedrecord from '../components/invitedrecord'
import Voucher from '../components/voucher'
import Distribution from '../components/distribution'




Vue.use(Router)

export default new Router({
	routes: [
		// {
		//   path: '/',
		//   name: '主页',
		//   component: HelloWorld
		// },
		// {
		//   path: '/index',
		//   name: '主页1',
		//   component: HelloWorld
		// },
		{
			path: '/',
			name: 'index',
			component: Index,
			// hidden: true,
			meta: {
				title: '首页',
				requireAuth: true
			}
		},
		{
			path: '/index',
			name: 'index',
			component: Index,
			// hidden: true,
			meta: {
				title: '首页',
				requireAuth: true
			}
		},

		{
			path: '/personage',
			name: 'personage',
			component: Personage,
			meta: {
				title: '排行榜',
				requireAuth: true
			}
		},
		{
			path: '/imya',
			name: 'imya',
			component: Imya,
			meta: {
				title: '个人',
				requireAuth: true
			}
		},
		{
			path: '/invitedrecord',
			name: 'invitedrecord',
			component: Invitedrecord,
			meta: {
				title: '邀请记录',
				requireAuth: true
			}
		},
		{
			path: '/voucher',
			name: 'voucher',
			component: Voucher,
			meta: {
				title: '购买凭证',
				requireAuth: true
			}
		},
		{
			path: '/distribution',
			name: 'distribution',
			component: Distribution,
			meta: {
				title: '分销海报',
				requireAuth: true
			}
		},
	]
})



