                hou: '00',
                min: '00',
                sec: '00',
                hm: '00',
                activity_poster: '',// 主题图片
                activity_price: '',//特价
                activity_title: '',//活动标题
                activity_content: '',//活动介绍
                activity_origin_price: '',//原价
                count: '',//排名共有多少人
                rank_list: [],//排名数组
                kkd: [
                    { avatar: "http://thirdwx.qlogo.cn/mmopen/vi_32/8jIrzdqccO4ibcu5BOEicy6wDib2BL3jxcHlCdcHvX1eqehGnfCSsmBNDZWaPZwYibIKjzt1AHiaEHBttfAaPeaZdicw/132", id: 1, nickname: "与自己为敌，为友！", order_money: "3.00", ranking: 1 },
                    { avatar: "http://thirdwx.qlogo.cn/mmopen/vi_32/u3L60uE6cv4F2GQ4NHfv6gyh73JaoWmGiaXqJolBahfLc9Ng3xQKePTQkBSLwyVGC4WzjQAAGS3xJ1pkX5cyeiaQ/132", id: 2, nickname: "与自己为敌，为友！", order_money: "2.00", ranking: 2 },
                    { avatar: "http://thirdwx.qlogo.cn/mmopen/vi_32/PkicocwC3IaZ04BbLmWr8h6fV4ZBMpgHF0rIr3gaibVLQTscTo0d9jyibS5aZERnzOc5A1b1BxicBeQoKR3Zgy4iczg/132", id: 3, nickname: "与自己为敌，为友！", order_money: "1.50", ranking: 3 },
                    { avatar: "http://thirdwx.qlogo.cn/mmopen/vi_32/8jIrzdqccO4ibcu5BOEicy6wDib2BL3jxcHlCdcHvX1eqehGnfCSsmBNDZWaPZwYibIKjzt1AHiaEHBttfAaPeaZdicw/132", id: 4, nickname: "与自己为敌，为友！", order_money: "1.00", ranking: 4 },
                ],


            }
        },
    

        //倒计时，原始位置，不可删除 （已加到请求完成后）
        // created() {
        //     var thisTime = this.actEndTime;
        //     thisTime = thisTime.replace(/-/g, '/');
        //     var time = new Date(thisTime).getTime();
        //     this.countDown(time);
        // },

        methods: {
            // 联系主办方 弹窗
            showPopup() {
                this.show = true;
            },
            // 跳转
            personagedm() {
                this.$router.push({ path: "personage" })

            },
            distributiondm() {
                this.$router.push({ path: "distribution" })
            },
            // 倒计时
            timeFormat(param) {
                return param < 10 ? '0' + param : param;
            },
            // 倒计时
            countDown(it) {

                var interval = setInterval(() => {
                    // 获取当前时间，同时得到活动结束时间数组
                    let newTime = new Date().getTime();
                    // 对结束时间进行处理渲染到页面
                    let endTime = new Date(it).getTime();
                    let obj = null;
                    // 如果活动未结束，对时间进行处理
                    if (endTime - newTime > 0) {
                        let time = (endTime - newTime) / 1000;
                        // 获取天、时、分、秒
                        let day = parseInt(time / (60 * 60 * 24));
                        let hou = parseInt(time % (60 * 60 * 24) / 3600);
                        let min = parseInt(time % (60 * 60 * 24) % 3600 / 60);
                        let sec = parseInt(time % (60 * 60 * 24) % 3600 % 60);
                        let hm = parseInt(time % (60 * 60 * 24) % 3600 % 60 * 100);
                        obj = {
                            day: this.timeFormat(day),
                            hou: this.timeFormat(hou),
                            min: this.timeFormat(min),
                            sec: this.timeFormat(sec),
                            hm: this.timeFormat(hm)
                        };
                        // console.log(obj.hm)
                        var kk = obj.hm + ""
                        // console.log(kk)
                        // console.log(kk.length)
                        var laststring = kk.substring(kk.length - 2);
                        // console.log(laststring)
                    } else { // 活动已结束，全部设置为'00'
                        obj = {
                            day: '00',
                            hou: '00',
                            min: '00',
                            sec: '00',
                            hm: '00'
                        };
                        // console.log(obj.hm)
                        clearInterval(interval);
                    }

                    // this.countDownList = obj.day + '天' + obj.hou + '时' + obj.min + '分' + obj.sec + '秒';

                    this.hou = obj.hou;
                    this.min = obj.min;
                    this.sec = obj.sec;
                    this.hm = laststring || obj.hm;
                }, 10);

            },



        },
        beforeCreate() {
            this.$axios.post("http://pdm.admin.quwutu.com/api/weixin/get_activity_detail", {}).then((res) => {
                console.log(res)
                this.activity_poster = res.data.list.activity_poster;
                this.activity_title = res.data.list.activity_title;
                this.activity_price = res.data.list.activity_price;
                this.activity_content = res.data.list.activity_content;
                this.activity_origin_price = res.data.list.activity_origin_price;
                this.actEndTime = res.data.list.stop_time;
                // this.actEndTime = "2020-06-23 18:50:00";
                // created() {
                var thisTime = this.actEndTime;
                thisTime = thisTime.replace(/-/g, '/');
                var time = new Date(thisTime).getTime();
                this.countDown(time);
                // },
                this.count = res.data.count;
                this.rank_list = res.data.rank_list;
                console.log(this.rank_list)



            })
        },
    }


</script>
