        width: 100px;
        height: 20px;
        /* background: green; */
        text-align: left;
        line-height: 20px;
        color: #f97541;
    }

    .base_limit {
        width: 154px;
        height: 29px;
        text-align: left;
        line-height: 29px;
        /* background: red; */
        font-size: 17px;
        font-weight: 600;
        color: #f95210
    }

    .fuhao {
        font-size: 13px;
    }

    .base_special {
        font-size: 24px;
    }

    .base_purchase {
        width: 119px;
        height: 40px;
        text-align: center;
        background-image: linear-gradient(to right, #ff3000, #ff7600);
        position: absolute;
        top: 10px;
        right: 20px;
        line-height: 40px;
        color: #ffffff;
        font-size: 18px;
        border-radius: 20px;
    }



    .base_time {
        position: absolute;
        top: -23px;
        right: 35px;
        width: 230px;
        height: 19px;
        line-height: 19px;
        font-size: 13px;
        background: #fa7140;
        padding: 0 5px;
        box-sizing: border-box;
        color: #ffffff;
        border-radius: 5px;
    }

    .base_time::after {
        content: '';
        width: 0;
        height: 0;
        border-left: 5px solid transparent;
        border-top: 5px solid #fa7140;
        border-right: 5px solid transparent;
        position: absolute;
        top: 18px;
        right: 70px;
        z-index: 100;
    }

    .lkhy {
        width: 100%;
        height: 80px;
    }
</style>


