<template>
    <!-- 邀请记录  -->
    <div class="invitedrecord">
        <div v-wechat-title='$route.meta.title'></div>
        <!-- <ul class="invited_ul">
            <li class="invited_img">
                <img src="../assets/3.png" alt="">
            </li>
            <li class="invited_name">
                <div class="ih_img">
                    <img src="../assets/3.png" alt="">
                </div>
                <div class="ih_name">
                    邀请记录邀请记录邀请记录邀请记录邀请记录邀请记录
                </div>
            </li>
            <li class="invited_money">0.3 <span>元</span></li>
        </ul> -->


        <ul class="invited_ul" v-for="(item,index) in invitedarr">
            <li class="invited_img">
                <img :src="item.pic" alt="">
            </li>
            <li class="invited_name">
                <div class="ih_img">
                    <img :src="item.pic" alt="">
                </div>
                <div class="ih_name">
                    {{item.name}}
                </div>
            </li>
            <li class="invited_money">{{1.2}}元</li>
        </ul>
        

       
      
        <Bottom></Bottom>
    </div>
</template>
<script>
    import Bottom from "./public/bottom.vue"
    export default {
        name: 'invitedrecord',
        data() {
            return {
                invitedarr:[],

            }

        },
        // 加载模块
        components: {
            "Bottom":Bottom
        },
        methods: {


        },
        beforeCreate: function () {
            this.$axios.get("./static/particulars.json", {}).then((res) => {
                console.log(res)
                // this.invitedarr=res.data[0].commodity.recommends.slice(0,6)
                this.invitedarr=res.data[0].commodity.recommends
                console.log(this.invitedarr)
            })
        },

    }
</script>
<style>
    .invitedrecord {
        background: #dedede;
        padding: 0 0 60px 0;
    }

    .invited_ul {
        width: 100%;
        height: 66px;
        background: #ffffff;
    }

    .invited_ul li {
        float: left;
        line-height: 50px;
        /* background: forestgreen; */
        margin-left: 10px;
    }

    .invited_ul:nth-of-type(2n) {
        margin-top: 10px;
    }

    .invited_img {
        width: 50px;
        height: 50px;
        margin-top: 7px;
        border-radius: 50%;
    }

    .invited_img img {
        width: 100%;
        height: 100%;
    }

    .invited_name {
        width: 200px;
        height: 50px;
        margin-top: 7px;
        /* background: yellow; */
        position: relative;
        line-height: 50px;
    }

    .invited_name .ih_img {
        position: absolute;
        width: 20px;
        height: 20px;
        top: 5px;
        left: 0;
    }

    .invited_name .ih_name {
        width: 180px;
        position: absolute;
        top: 0;
        left: 20px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;

    }

    .invited_name .ih_img img {
        width: 100%;
        height: 100%;
    }

    .invited_money {
        width: 84px;
        margin-top: 7px;
        text-align: center;
        color: coral;
        line-height: 50px;
        overflow: hidden;

    }
   
</style>


