<template>
    <div class="distribution">
        <!-- 分销海报 -->
        <div v-wechat-title='$route.meta.title'></div>
        <ul class="dist_hade">
            <!-- 提示 -->
            <li class="dist_hint">
                <img src="../assets/3.png" alt="">
            </li>
            <li class="dist_text">关注公众号&nbsp;及时接收奖励金到账通知</li>
            <li class="dist_attention">
                <div @click=" distrishowPopup">马上关注</div>
            </li>
            <!-- 关注弹窗 二维码 -->
            <van-popup class="attention_box" v-model="show">
                <div class="attention_code">
                    <img src="../assets/3.png" alt="">
                </div>
                <p class="attention_p">关注即可收到收益提醒</p>
            </van-popup>
        </ul>
        <div class="dist_imgbox">
            <img src="../assets/5.png" alt="">
        </div>
        <div class="dist_earnings">恭喜您获得分销资格，请长按保存图片<br>每推广一位好友下单，您立即获得<span>0.3</span>元&nbsp;收益</div>
        <!-- 返回 按扭 -->
        <div class="return_button" @click="retindex()">
            <img src="../assets/3.png" alt="">
            <p></p>
        </div>

    </div>
</template>
<script>

    export default {
        name: 'distribution',
        data() {
            return {
                show: false,

            }
        },
        methods: {
            retindex() {
                this.$router.push({ path: "index" })
            },
            distrishowPopup() {
                this.show = true;
            },

        },
    }
</script>
<style>
    .distribution {
        background: rgba(16, 16, 16, 0.88);
    }

    .dist_hade {
        width: 100%;
        height: 50px;
        background: #faf3eb;
    }

    .dist_hade li {
        /* line-height: 50px; */
        float: left;
    }

    .dist_hint {
        width: 25px;
        height: 25px;
        /* background: green; */
        margin-top: 14px;
        margin-left: 18px;
    }

    .dist_hint img {
        display: table-cell;
        width: 100%;
        height: 100%;
    }

    .dist_text {
        width: 230px;
        height: 25px;
        /* background: blue; */
        font-size: 13px;
        line-height: 25px;
        margin-top: 14px;
        margin-left: 10px;

    }

    .dist_attention {
        width: 83px;
        height: 34px;
        background: #f54d25;
        text-align: center;
        line-height: 34px;
        color: #ffffff;
        margin-top: 8px;
        border-radius: 10px;
        font-size: 13px;
    }

    /* img */
    .dist_imgbox {
        width: 262px;
        margin: 20px auto 0;
    }

    .dist_imgbox img {
        width: 100%;
    }



    .dist_earnings {
        width: 100%;
        height: 77px;
        padding: 20px 0;
        font-size: 16px;
        background: #f5f5f5;
        text-align: center;
        line-height: 20px;
        box-sizing: border-box;
    }

    .return_button {
        width: 46px;
        height: 46px;
        position: fixed;
        top: 60%;
        right: 10px;
        border-radius: 50%;
        z-index: 2020;
    }

    .return_button img {
        width: 100%;
        height: 100%;
    }







    .attention_box {
        border-radius: 5px;
        width: 230px;
        height: 260px;
        padding: 10px;
        box-sizing: border-box;

    }

    .attention_code {
        width: 200px;
        height: 200px;
        margin: auto;

    }

    .attention_code img {
        width: 100%;
        height: 100%;
    }

    .van-modal {
        position: fixed;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-color: rgba(0, 0, 0, .5);
    }

    .attention_p {
        height: 35px;
        line-height: 35px;
        text-align: center;
        margin-top: 7px;
        font-size: 15px;
        color: #9f9f9f;

    }
</style>


