<template>
    <!-- 个人 -->
    <div class="personage">
        <div v-wechat-title='$route.meta.title'></div>
        <!-- 个人信息 -->
        <ul class="informations">
            <li class="toimg"><img src="../assets/3.png" alt=""></li>
            <li class="inforname">
                <p>Red_engine</p>
                <p>排名10</p>
            </li>
            <li class="remind" @click="persoPopup">
                <div><img src="../assets/3.png" alt=""></div>
                <p>收益提醒</p>
            </li>
            <!-- 关注弹窗 二维码 -->
            <van-popup class="perso_box" v-model="show">
                <div class="perso_code">
                    <img src="../assets/3.png" alt="">
                </div>
                <p class="perso_p">关注即可收到收益提醒收到收益提醒</p>
            </van-popup>
        </ul>
        <!-- 提现 -->
        <div class="persWithdraw_box">
            <ul class="persWithdraw">
                <li class="account">
                    <p>￥<span>0.00</span></p>
                    <p>已到账</p>
                </li>
                <li class="canDeposit">
                    <p>￥<span>0.00</span></p>
                    <p>可提现</p>
                </li>
                <li class="deposit">
                    <div>提现</div>
                </li>
            </ul>
        </div>
        <!-- 邀请 明细 -->
        <div class="invitation">
            <p class="invit_detail" @click="invitedrecorddm()">邀请明细 <span class="yytk">></span></p>
            <ul class="invitation_ul">
                <!-- 邀请人数 -->
                <li class="invitation_invite" @click="invitedrecorddm()">
                    <ul>
                        <li>
                            <img src="../assets/3.png" alt="">
                        </li>
                        <li>
                            <p>邀请人数</p>
                            <p class="quantity">110</p>
                        </li>
                    </ul>
                </li>
                <!-- 分销订单 -->
                <li class="invitation_distri" @click="invitedrecorddm()">
                    <ul>
                        <li>
                            <img src="../assets/3.png" alt="">
                        </li>
                        <li>
                            <p>分销订单</p>
                            <p class="quantity">110</p>
                        </li>
                    </ul>
                </li>
            </ul>

        </div>
        <!-- 收益 排行榜 -->
        <div class="earnings_ranking">
            <ul class="rankingHeadr">
                <li class="rankingImg">
                    <img src="../assets/3.png" alt="">
                </li>
                <li class="rankingText">收益排行榜</li>
            </ul>
            <!-- 排序 -->
            <p class="sort"><b><span>排名</span><span>用户名称</span><span>收益</span></b></p>
            <ul class="pers_ul">
                <!-- 个人 -->
                <!-- <li>
                    <ul class="pers_personage">
                        <li class="pers_three">
                            <img src="../assets/3.png" alt="">
                        </li>
                        <li class="pers_img">
                            <img src="../assets/3.png" alt="">
                        </li>
                        <li class="pers_name">名字可能会长一点，像这样</li>
                        <li class="pers_money">
                            累计收益<span>0.6</span>元
                        </li>
                    </ul>
                </li> -->
                <!-- 个人 -->
                <li v-for="(item , index) in rank_list" v-if="index < 3">
                    <ul class="pers_personage">
                        <li class="pers_three">
                            <img src="../assets/3.png" alt="">
                        </li>
                        <li class="pers_img">
                            <img :src="item.avatar" alt="">
                        </li>
                        <li class="pers_name">{{item.nickname}}</li>
                        <li class="pers_money">
                            累计收益<span>{{item.order_money}}</span>元
                        </li>
                    </ul>
                </li>
                <!-- 个人 -->
                <li v-for="(item , index) in rank_list.slice(3)">
                    <ul class="pers_personage">
                        <li class="pers_three">
                            {{item.ranking}}
                        </li>
                        <li class="pers_img">
                            <img :src="item.avatar" alt="">
                        </li>
                        <li class="pers_name">{{item.nickname}}</li>
                        <li class="pers_money">
                            累计收益<span>{{item.order_money}}</span>元
                        </li>
                    </ul>
                </li>

            </ul>

        </div>
        <Bottom></Bottom>


    </div>
</template>
<script>
    import Bottom from "./public/bottom.vue"
    export default {
        name: 'personage',
        data() {
            return {
                show: false,
                rank_list: [],//排名数组

            }

        },
        // 加载模块
        components: {
            "Bottom":Bottom
        },
        methods: {
            invitedrecorddm() {
                this.$router.push({ path: "invitedrecord" })
            },
            persoPopup() {
                this.show = true;
            }

        },
        beforeCreate() {
            this.$axios.post("http://pdm.admin.quwutu.com/api/weixin/get_activity_detail", {}).then((res) => {
                console.log(res)
                this.rank_list = res.data.rank_list;
                console.log(this.rank_list)



            })
        },


    }
</script>
<style>
    .personage {

        background: #fcfafb;
    }

    .informations {
        width: 100%;
        height: 97px;
        background: #ffffff;
    }

    .informations li {
        /* width: 60px; */
        /* height: 60px; */
        /* background: green; */
        float: left;
        margin-left: 10px;

    }

    .toimg {
        width: 60px;
        height: 60px;
        margin-top: 20px;
    }

    .toimg img {
        width: 100%;
        height: 100%;
    }

    .inforname {
        width: 50%;
        height: 60px;
        margin-top: 20px;
        line-height: 27px;
    }

    .inforname p:nth-of-type(1) {
        font-size: 20px;
    }

    .inforname p:nth-of-type(2) {
        font-size: 16px;
    }

    .remind {
        width: 90px;
        height: 60px;
        margin-top: 20px;
        text-align: center;
        border-left: 2px solid #eeeeee;

    }

    .remind div {
        width: 26px;
        height: 26px;
        margin: 0 auto 10px;
    }

    .remind div img {
        width: 100%;
    }

    .persWithdraw_box {
        width: 100%;
        overflow: hidden;
        background: #ffffff;
    }

    .persWithdraw {
        width: 97%;
        height: 69px;
        background-image: linear-gradient(to right, #ff3000, #ff7600);
        /* background: #db5724; */
        margin: 6.3px auto;
        color: #ffffff;
        border-radius: 10px;
        padding: 6.9px 15px;
        box-sizing: border-box;
    }

    .persWithdraw li {
        float: left;
        font-size: 17px;
    }

    .account {
        width: 100px;
        height: 100%;
        /* background: green;lk */
    }

    .account p {
        margin-top: 6px;
    }

    .canDeposit {
        width: 100px;
        height: 100%;
        /* background: green; */

    }

    .canDeposit p {
        margin-top: 6px;
    }

    .deposit {
        width: 81px;
        height: 100%;
        /* background: red; */
        margin-left: 44px;

    }

    .deposit div {
        width: 81px;
        height: 31px;
        background: #ffffff;
        text-align: center;
        line-height: 31px;
        margin: 13px auto 0;
        border-radius: 31px;
        color: #ca5043;
    }

    .invitation {
        width: 100%;
        height: 158px;
        background: #FFFFFF;
        padding: 10px;
        box-sizing: border-box;
    }

    .invit_detail {
        width: 100%;
        height: 29px;
        /* background: green; */
        font-size: 20px;
        line-height: 29px;
    }

    .yytk {
        /* display: inline-block; */
        float: right;
        font-size: 20px;
    }

    .invitation_ul {
        width: 100%;
        height: 97px;
        /* background: green; */
        margin-top: 10px;
    }

    .invitation_invite {
        width: 48%;
        height: 100%;
        float: left;
        background: rgba(253, 224, 207, 0.5);
        border-radius: 10px;
    }

    .invitation_distri {
        width: 48%;
        height: 100%;
        background: rgba(253, 224, 207, 0.5);
        float: right;
        border-radius: 10px;

    }

    .invitation_invite ul {
        width: 100%;
        height: 100%;
    }

    .invitation_invite li {
        float: left;
    }

    .invitation_invite li:nth-of-type(1) {
        width: 41px;
        height: 100%;
        margin-left: 10px;
        margin-top: 26px;
    }

    .invitation_invite li:nth-of-type(2) {
        width: 93px;
        height: 55px;
        margin-left: 7px;
        margin-top: 17px;
        text-align: center;
        /* background: red; */
        font-size: 19px;
    }

    .invitation_invite li img {
        width: 100%;
    }

    .quantity {
        margin-top: 10px;
        font-size: 27px;
        color: #d53c28;

    }

    /*  */
    .invitation_distri ul {
        width: 100%;
        height: 100%;
    }

    .invitation_distri li {
        float: left;
    }

    .invitation_distri li:nth-of-type(1) {
        width: 41px;
        height: 100%;
        margin-left: 10px;
        margin-top: 26px;
    }

    .invitation_distri li:nth-of-type(2) {
        width: 93px;
        height: 55px;
        margin-left: 7px;
        margin-top: 17px;
        text-align: center;
        /* background: red; */
        font-size: 19px;
    }

    .invitation_distri li img {
        width: 100%;
    }

    /*  */
    .earnings_ranking {
        width: 100%;
        min-height: 75px;
        background: #ffffff;
        margin: 6.3px auto;
        /* color: #ffffff; */
        border-radius: 10px;
        padding: 6.9px 15px;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
    }

    .rankingHeadr {
        width: 142px;
        height: 30px;
        /* background: red; */
        margin: 10px auto 0;
    }

    .rankingHeadr li {
        float: left;
    }

    .rankingImg {
        width: 30px;
        height: 30px;
        /* background: green; */
    }

    .rankingImg img {
        width: 100%;
    }

    .rankingText {
        width: 111px;
        height: 30px;
        /* background: blue; */
        text-align: center;
        line-height: 30px;
        font-size: 20px;
        color: #ff6947;
        font-weight: 900;
    }

    .sort {
        width: 100%;
        height: 30px;
        line-height: 30px;
        margin-top: 10px;
        /* background: green; */
    }

    .sort span {
        padding: 0 2px;
        display: inline-block;
        min-width: 40px;
        height: 100%;
        /* background: red; */
        margin-left: 5px;
        text-align: center;
    }

    .sort span:nth-of-type(3) {
        float: right;
        margin-right: 10px;
    }

    .pers_ul {
        width: 100%;
        min-height: 30px;
        /* background: red; */
        margin-top: 20px;
        margin-bottom: 50px;
        overflow: hidden;
    }

    .pers_ul>li {
        width: 100%;
        height: 100%;
        margin: 10px 0 20px;
        /* background: green; */
    }

    .pers_personage {
        width: 100%;
        height: 30px;
    }

    .pers_personage>li:nth-of-type(1) {
        width: 20px;
        height: 20px;
        margin-left: 10px;

    }

    .pers_img {
        width: 30px;
        height: 30px;
        margin-left: 16px;
    }

    .pers_name {
        width: 126px;
        height: 100%;
        line-height: 30px;
        margin-left: 10px;
        font-size: 12px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        /* background: blue; */
    }

    .pers_personage>li {
        float: left;
    }

    .pers_personage>li img {
        width: 100%;
    }

    .pers_money {
        width: 103px;
        height: 100%;
        font-size: 12px;
        text-align: center;
        line-height: 30px;
        margin-left: 20px;
        color: #e75331;
    }

    .pers_three {
        width: 30px;
        height: 30px;
        text-align: center;
        line-height: 30px;
    }

    .pers_three img {
        width: 100%;
    }

    .examine {
        width: 100%;
        height: 30px;
        /* background: red; */
        color: #ff6947;
        text-align: center;
        line-height: 30px;
        font-size: 14px;
    }

    /*  */
    .perso_box {
        border-radius: 5px;
        width: 230px;
        min-height: 260px;
        padding: 10px;
        box-sizing: border-box;
    }

    .perso_code {
        width: 200px;
        height: 200px;
        margin: auto;
    }

    .perso_code img {
        width: 100%;
        height: 100%;
    }

    .perso_p {
        min-height: 35px;
        line-height: 20px;
        text-align: center;
        margin-top: 15px;
        font-size: 15px;
        color: #9f9f9f;

    }
</style>


