<template>
    <div class="bottom">
        <ul class="footer">
            <li class="bottom_page" @click="indexdm()">
                <div>
                    <img src="../../assets/3.png" alt="">
                </div>
                <p>首页</p>
            </li>
            <li class="bottom_service">
                <div>
                    <img src="../../assets/3.png" alt="">
                </div>
                <p>客服</p>
            </li>
            <li class="bottom_more" @click="distributiondm()">点我赚更多奖金</li>
        </ul>
    </div>
</template>
<script>
    export default {
        name: 'bottom',
        data() {
            return {

            }

        },
        methods: {
            indexdm(){
                this.$router.push({ path: "index" })
            },
            distributiondm() {
                this.$router.push({ path: "distribution" })
            },


        },
    }
</script>
<style>
    .footer {
        position: fixed;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 50px;
        background: #ffffff;
        padding: 4px 10px;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
    }

    .footer li {
        float: left;
        margin-left: 10px;
    }

    .bottom_page {
        width: 25px;
        height: 100%;
        /* background: green; */
        font-size: 10px;
        line-height: 15px;
        color: #ff7600;
    }

    .bottom_page div {
        width: 100%;
    }

    .bottom_page div img {
        width: 100%;
    }

    .bottom_service {
        width: 25px;
        height: 100%;
        /* background: green; */
        font-size: 10px;
        line-height: 15px;
    }

    .bottom_service div {
        width: 100%;
    }

    .bottom_service div img {
        width: 100%;
    }

    .bottom_more {
        position: absolute;
        width: 246px;
        height: 85%;
        /* background: hotpink; */
        text-align: center;
        line-height: 45px;
        right: 20px;
        font-size: 14px;
        color: #ffffff;
        border-radius: 45px;
        background-image: linear-gradient(to right, #ff7600, #ff3000);

    }
</style>


