<template>
    <div class="voucher">
        <!-- 购买凭证 -->
        <!-- 支付 成功 -->
        <div class="payments">
            <div class="box payments_box1">1</div>
            <div class="box payments_box2">2</div>
            <div class="box payments_box3">3</div>
            <div class="boxte payments_text1">支付成功</div>
            <div class="boxte payments_text2">到店</div>
            <div class="boxte payments_text3">兑换成功</div>
            <!-- 进度 -->
            <div class="schedule1"></div>
            <div class="schedule2"></div>
        </div>
        <!-- 二维码 -->
        <div class="code_box">
            <div class="code_code">
                <img src="../assets/3.png" alt="">
            </div>
            <p class="code_text">到店向工作人员出示“二维码”<br>此码一次有效，请勿泄露</p>

        </div>
    </div>
</template>
<script>
    export default {
        name: 'voucher',
        data() {
            return {

            }
        }
    }
</script>
<style>
    .voucher {
        background: #f1f1f1;
    }

    .payments {
        width: 100%;
        height: 100px;
        background: #ffffff;
        position: relative;
    }

    .payments .box {
        width: 35px;
        height: 35px;
        text-align: center;
        line-height: 35px;
        border-radius: 50%;
        color: #ffffff;
        background: #FF9800;
    }

    .payments .payments_box1 {
        position: absolute;
        top: 17px;
        left: 51px;
    }

    .payments .payments_box2 {
        position: absolute;
        top: 17px;
        left: 0;
        right: 0;
        margin: auto;
        background: rgba(212, 211, 211, 0.79);
    }

    .payments .payments_box3 {
        position: absolute;
        top: 17px;
        left: 300px;
        background: rgba(212, 211, 211, 0.79);
    }

    .payments .boxte {
        position: absolute;
        width: 80px;
        height: 30px;
        line-height: 30px;
        /* background: green; */
        text-align: center;
        top: 55px;
    }

    .payments_text1 {
        left: 30px;
    }

    .payments_text2 {
        left: 0;
        right: 0;
        margin: auto;
    }

    .payments_text3 {
        left: 277px;
    }

    .schedule1 {
        position: absolute;
        top: 31px;
        left: 86px;
        width: 84px;
        height: 7px;
        background: #dddcdc;
    }

    .schedule2 {
        position: absolute;
        top: 31px;
        left: 205px;
        width: 95px;
        height: 7px;
        background: #dddcdc;
    }

    .code_box {
        width: 100%;
        height: 260px;
        background: #ffffff;
        margin-top: 10px;
        position: relative;
    }

    .code_code {
        width: 130px;
        height: 130px;
        /* background: green; */
        position: absolute;
        top: 50px;
        left: 0;
        right: 0;
        margin: auto;
    }

    .code_code img {
        width: 100%;
        height: 100%;
    }
    .code_text{
        width: 100%;
        position: absolute;
        text-align: center;
        top: 200px;
        left: 0;
    }
</style>


