<template lang="html">
	<form action="/">
	  	<van-nav-bar
		  :title="Title"
		  left-text="返回"
		  right-text="按钮"
		  left-arrow
		  @click-left="onClickLeft"
		  @click-right="onClickRight"
		/>
	</form>
</template>
<script>
	import { Toast } from 'vant';
	export default {
		props: ['Title'],
	  // 数据
	  	data() {
		    return {
		      	value: ""
		    };
	  	},
	  	// 当数据发生改变时
	  	watch: {},
		// 方法
		methods: {
	    	onClickLeft() {
		      	Toast.success('点击返回');
		    },
		    onClickRight() {
		      	Toast.fail('点击返回');
		    },
	  	},
	  	// 路由发生变化时
	  	beforeRouteLeave (to, from, next) {
	    	var conent = this;
	    	next()
	  	},
	  	// 加载模块
	  	components:{},
	  	// 加载执行
	  	created:function(){ 

	  	}
	};
</script>


